import swopList from './base/swopList.vue'
import interfaces from '../../../utils/interface.js';
export default {
	data() {
		return {
			categoryList: [], //分类列表
			hotList: [], //商品列表
			showCategoryIndex: 0, //显示类目
			selectIndex : 1, //热销价格切换
			rotate_x : true ,//销量排序 （true降序 false升序）
			rotate_j : false, //价格排序 
		}
	},
	onLoad() {
		this.initData()
	},
	methods: {
		async initData() {
			this.request({
				url: interfaces.get_bigclass, //获取分类名称
				success: ((res) => {
					if (res.error_code == 0) {
						this.categoryList = res.data;
					}
				})
			});
			this.request({
				url: interfaces.get_hotgoods, //获取热卖商品
				method: "POST",
				success: ((res) => {
					console.log(res.data)
					this.hotList = res.data;
				})
			});
		},
		showCategory(item) {
			//分类切换显示
			this.showCategoryIndex = item.id - 1;

			// uni.showLoading({
			// 	title: '加载中...'
			// });
		},
		showIndx(e){
			this.selectIndex = e;
		}, 
		buy(){//购买会员
			console.log('会员')
			uni.navigateTo({
				url:'./base/buyMember'
			})
		},
		spin(e){//排序
			if(e){//如果是0 改变销量排序 否则改变价格	
				this.rotate_x = !this.rotate_x;  //销量排序
			}else{
				this.rotate_j = !this.rotate_j;  //价格排序
				if(this.rotate_j){
					this.hotList = this.hotList.sort((a , b) => { //升序 原生js数组sort方法
						return a.price - b.price
					})
				}
				else{
					this.hotList = this.hotList.sort((a , b) => { //降序
						return b.price - a.price
					})
				}
			}
		}
	},
	components: {
		swopList
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh();
			this.initData();
		}, 1000);
	},
}
